* {
    font-family:"Montserrat",sans-serif
}

h2 {
    font-size: 1.5vw;
}

h2.personal {
    text-align: center;
}

h3 {
    font-size: 1.2vw;

}

p {
    font-size: 1vw;
}

body {
    background-color:  #c6c6c6;
}
#root {
    display: flex;
    flex-flow: column wrap;
    height: 100%;
    max-height: 1500px;
justify-content: flex-start;
    margin-top: 100px;
    margin-right: 100px;

   
}
hr {
width: 80%;
color: black;
}

.output {
  text-align: center;
    color:    #eaeaea;
    background-color: #5564d3;
    width: 100%;
    padding: 20px 0;
    
}

h3.profile {
    text-decoration: underline;
}

.profile-text {
    margin: 0 auto;
    width: 100%;
    max-width: 700px;
    word-wrap: break-word;
}

.cv {
  
    height: 100%;
    min-height: 1000px;
    max-height: 1400px;
    width: 100%;
    max-width: 800px;
    background-color:  #f9f9f9;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}


.jobs-list {
    text-align: center;
}
p.job-para {
    display: inline;
    font-weight: 700;
}
.inputs {
    display: flex;
    flex-flow: column nowrap;
    width: 50%;
    max-width: 700px;
height: 100%;
max-height: 1400px;
    justify-content: flex-start;
    align-items: center;
    margin: 0 100px;
    background-color:  #f9f9f9;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}
.info, .work-exp, .education-inputs {
    margin: 20px auto 10px auto;
    display: flex;
    flex-flow: row nowrap;
    width: 90%;
justify-content: center;
    position: relative;
}


.inst-divs, .job-divs {
margin: 0 0 30px 0;
    overflow: scroll;
    max-height: 500px;
}

.inst-divs {
    margin-bottom: 50px;
}
form {
    margin-bottom: 40px;
}

button.details-submit {
    margin-top: -3vw;
    width: 8vw;
}

.work-exp textarea {
    margin-bottom: 30px;
}



button {
    font-size: 20px;
padding: 5px;
background-color:  #424242;
color: white;
font-size: 1.2vw;
border: none;
border-radius: 5px;
}

input {
    width: 100%;
    height: 20px;
    font-size: 15px;
    border-radius: 5px;
}

textarea {
    width: 100%;
    font-size: 15px;
    border-radius: 5px;
}

.info input {
margin-bottom: 40px;
}

.work-exp input {
    margin-bottom: 40px;
}

.education-inputs input {
    margin-bottom: 40px;
}

.info button {
  
   position: absolute;
   left: 0;
   top: 70vh;
  
}

.work-exp button.submit-job {
    position: absolute;
    left: 0;
    top: 42vh;
}

button.edit-job {
   position: absolute;
   left: 90px;
   top: 42vh;
}

button.remove-job {
    position: absolute;
    left: 180px;
    top: 42vh;
}

button.new-job {
    margin: 30px;
   
 }

 button.new-inst {
    margin: 0 auto 0 10px;
}

.inst-list h3 {
    text-align: center;
}


button.remove-inst {
    position: absolute;
 top: 25.8vh;
    left: 7vw;
}

button.edit-info {
 margin: -3vw 0 0 170px;
}



.job-inputs input {
 margin: 50px;
 

}

table.inst-table {
    width: 100%;
    max-width: 700px;
    margin: 0 auto 30px auto;
    text-align: center;
}

table, th, td {
    border: 1px solid black;
}